"use client";

import { useState, useEffect } from 'react';

const ExchangeRate = () => {
  const [rate, setRate] = useState<string>('Î°úÎî©Ï§ë...');
  const [title, setTitle] = useState<string>('');

  useEffect(() => {
    const fetchExchangeRate = async () => {
      try {
        // 1) Dunamu (Îπ†Î•¥Í≥† Ï†ïÌôï, ÌïòÏßÄÎßå CSP/connect-srcÍ∞Ä ÌóàÏö©ÎêòÏñ¥Ïïº Ìï®)
        try {
          const response = await fetch('https://quotation-api-cdn.dunamu.com/v1/forex/recent?codes=FRX.KRWUSD', {
            method: 'GET',
            headers: { 'Accept': 'application/json' }
          });
          if (!response.ok) throw new Error(`Dunamu HTTP ${response.status}`);
          const data = await response.json();
          if (data && data.length > 0 && data[0].basePrice) {
            const rateValue = data[0].basePrice;
            setRate(Math.round(rateValue).toLocaleString() + 'Ïõê');
            setTitle('ÏóÖÎç∞Ïù¥Ìä∏: ' + new Date().toLocaleString('ko-KR') + ' (USD/KRW)');
            return;
          }
          throw new Error('Dunamu data missing');
        } catch (e) {
          // fallthrough to open.er-api.com
          console.warn('Dunamu API failed, falling back...', e);
        }

        // 2) Fallback: open.er-api.com (CORS friendly)
        try {
          const fallback = await fetch('https://open.er-api.com/v6/latest/USD', {
            method: 'GET',
            headers: { 'Accept': 'application/json' }
          });
          if (!fallback.ok) throw new Error(`Fallback HTTP ${fallback.status}`);
          const fx = await fallback.json();
          const rateValue = fx && fx.rates && fx.rates.KRW ? fx.rates.KRW : null;
          if (!rateValue) throw new Error('Fallback data missing');
          setRate(Math.round(rateValue).toLocaleString() + 'Ïõê');
          setTitle('ÏóÖÎç∞Ïù¥Ìä∏: ' + new Date().toLocaleString('ko-KR') + ' (USD/KRW, fallback)');
        } catch (e) {
          console.warn('Fallback API failed, using default value...', e);
          throw e;
        }
      } catch (error) {
        console.error('ÌôòÏú® Ï†ïÎ≥¥Î•º Í∞ÄÏ†∏Ïò§Îäî Ï§ë Ïò§Î•ò Î∞úÏÉù:', error);
        // Ïò§Î•ò Î∞úÏÉù Ïãú Í∏∞Î≥∏ Î©îÏãúÏßÄ ÌëúÏãú (ÌïòÎìúÏΩîÎî©Îêú Í∞í)
        setRate('1,350Ïõê');
        setTitle('ÌôòÏú® Ï†ïÎ≥¥Î•º Í∞ÄÏ†∏Ïò¨ Ïàò ÏóÜÏäµÎãàÎã§. (Í∏∞Î≥∏Í∞í)');
      }
    };

    fetchExchangeRate();
    const interval = setInterval(fetchExchangeRate, 3600000); // 1ÏãúÍ∞ÑÎßàÎã§ ÏóÖÎç∞Ïù¥Ìä∏

    return () => clearInterval(interval);
  }, []);

  return (
    <span id="exchange-rate" title={title} className="font-bold text-[#ffcc00]">
      {rate}
    </span>
  );
};

export default ExchangeRate;
